.telegram {
    text-decoration: none;
    color: black;
  }
  .divbymuslima {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .contact {
    background: #ececec;
    border-radius: 1rem;
    margin-top: 3rem;
    height: max-content;
    width: 60%;
    margin-left: 8rem;
    padding: 0px 15px 25px 15px;
  }
  .header {
    padding-top: 2rem;
    h1 {
      font-size: 2rem;
    }
  }
  .section {
    margin-top: 1rem;
    align-items: center;
    display: flex;
    justify-content: space-between;
    h3 {
      font-size: 1.3rem;
    }
  }
  .sectionTwo {
    h4 {
      font-size: 1rem;
    }
    a {
      text-decoration: underline;
      color: blue;
    }
  }
  .sending {
    margin-bottom: 0px;
    margin-top: 8px;
    height: 20px;
  }
  .or {
    background: #ececec;
    margin-top: 3rem;
    margin-left: -15.5rem;
    width: 68%;
    border-radius: 1rem;
  }
  .form {
    padding: 25px 20px 15px 20px;
  }
  .form {
    .name {
      padding: 9px 10px;
      border-radius: 1rem;
      margin: 15px 5px;
    }
    .phone {
      padding: 9px 10px;
      border-radius: 1rem;
      margin: 15px 5px;
    }
    .second {
      padding: 9px 10px;
      border-radius: 1rem;
      margin: 15px 5px;
    }
    .message {
      padding: 1rem;
      border-radius: 1rem;
      border: 2px solid black;
      width: 93%;
      height: 95px;
      margin-bottom: 20px;
    }
    .submit {
      background: fixed;
      border: none;
      padding: 5px 40px;
      background: black;
      border-radius: 0.5rem;
      color: white;
      cursor: pointer;
    }
  }
  .djalobi {
    h3 {
      padding-top: 2rem;
      text-align: center;
    }
  }
