#inputs {
  padding: 1rem;
  border-radius: 10px;
  border: 1px solid #873600;
  margin-top: 1rem;
  width: 70%;
}
.form{
    margin-left: 15rem;
    h1{
        font-weight: 400;
        font-size: 28px;
    }
  #but{
    padding: 1rem 3rem;
    border-radius: 10px;
    border: 1px solid #873600;
    margin-top: 1rem;
    margin-left: 40.3rem;
  }  
}
@media all and (max-width: 500px) {
  #inputs{
    padding: 1rem;
    border-radius: 10px;
    border: 1px solid #873600;
    width: 50%;
  }
  .form{
    margin: auto;
    h1{
      font-weight: 300;
      font-size: 24px;
    }
  }
}