#birbalo{
    padding: 1rem;
    border-radius: 10px;
    border: 1px solid #873600;
    margin-top: 1rem;
    width: 50%;
}
.h1{
    display: flex;
    justify-content: center;
    font-size: 36px;
    font-weight: 500;
}
.h2{
    display: flex;
    justify-content: center;
    margin-top: 2rem;
    margin-bottom: -1rem;
    font-size: 36px;
    font-weight: 500;
}
.pov{
    display: flex;
    justify-content: center;
    margin-top: 1rem;
    font-size: 12px;
    font-weight: 300;
    span{
        font-weight: 800;
    }
}
.forms{
    display: flex;
    margin-top: 2rem;
    flex-direction: column;
    align-items: center;
}
.head {
    width: 100%;
    display: flex;
    justify-content: space-around;
    border: 1px solid #873600;
    border-radius: 10px;
    .headimg {
        width: 20%;
        padding: 10px;
        img {
            width: 100%;
            border-radius: 1rem;
        }
    }
    .card-body{
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
    .foot{
        display: flex;
        gap: 3rem;
        align-items: center;
        padding-right: 10px;
        button{
            border: none;
            border-radius: 10px;
            padding: 10px;
        }
        .deletebtn{
            background: #ff0000;
            color: #fff;
        }
    }
}