.fillialOne {
    display: flex;
    justify-content: space-between;
    border: none;
    border-radius: 1rem;
    background: #873600;
    padding: 1rem;
    align-items: center;
    text-decoration: none;
    div {
      padding-left: 20px;
      h4 {
        font-size: 24px;
        font-weight: 700;
        color: white;
      }
      h6 {
        font-size: 18px;
        font-weight: 600;
        color: white;
      }
      p {
        font-size: 16px;
        color: white;
        font-weight: 400;
      }
      .icon {
        color: white;
      }
    }
  }
  .fillialTwo {
    margin-top: 3rem;
    display: flex;
    justify-content: space-between;
    border: none;
    border-radius: 1rem;
    background: #873600;
    padding: 1rem;
    align-items: center;
    text-decoration: none;
    div {
      padding-left: 20px;
      h4 {
        font-size: 24px;
        font-weight: 700;
        color: white;
      }
      h6 {
        font-size: 18px;
        font-weight: 600;
        color: white;
      }
      p {
        font-size: 16px;
        color: white;
        font-weight: 400;
      }
      .icon {
        color: white;
      }
    }
  }
  .header {
    h1 {
      font-size: 60px;
      font-weight: 600;
      font-family: "Times New Roman", Times, serif;
      display: flex;
      justify-content: center;
      margin-top: 3rem;
    }
  }
  .section{
    display: flex;
    flex-direction: column;
  }
