.korzina{
  background: #f6f8f9;
}
.savat {
  border: 2px solid #873600;
  width: 100%;
  height: max-content;
  border-radius: 10px;
  display: flex !important;
  gap: 10px;
  align-items: center;
  text-align: center;
  justify-content: flex-start;
  margin: auto;
  margin-top: 3rem;
  margin-bottom: 3rem;
  padding: 20px;
}
.savat {
  display: flex;
  justify-content: space-between;
  align-items: center;
  .li {
    width: 20%;
    img {
      width: 100%;
    }
  }
}
.minusBtns {
  gap: 5rem;
}
.plusBtns {
  gap: 5rem;
}
.btns {
  display: flex;
  gap: 3rem;
  align-items: center;
  text-align: center;
  .plusminus {
    display: flex;
    gap: 3rem !important;
    h3 {
      color: #000;
    }
    .minusBtns,
    .plusBtns {
      display: flex;
      gap: 1rem;
    }
    .minus,
    .plus {
      width: 35px;
      height: max-content;
      border: none;
      border: 1px solid #873600;
      background: #873600;
      color: #fff;
      align-items: center;
      text-align: center;
      border-radius: 0.3rem;
    }
  }
}
.remove-btn .remove {
  width: 75px;
  height: 30px;
  margin: auto;
  font-size: 14px;
  border: none;
  background: #873600;
  border-radius: 5px;
  color: #fff;
}
.totalprice {
  p {
    color: #000;
    font-size: 15px;
    font-weight: 500;
  }
}
.title {
  p {
    font-size: 15px;
    font-weight: 600;
  }
}
.register {
  background: #873600;
  border-radius: 10px;
  align-items: center;
  width: 50%;
  margin: auto;
  padding-bottom: 1rem;
  div {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 1rem;
    .tootalprice {
      color: white;
      font-size: 20px;
      font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
      margin-bottom: -10px;
    }
    input {
      width: 90%;
      height: 10vh;
      border-radius: 10px;
      border: none;
    }
    button {
      padding: 1rem;
      border: none;
      border-radius: 10px;
    }
  }
}

