.footer {
  margin-top: 5rem;
  width: 100%;
  height: max-content;
  background: #873600;
  align-items: center;
  color: #fff;
  .header {
    a {
      display: flex;
      justify-content: center;
      img {
        width: 10%;
      }
    }
  }
  .se {
    display: flex;
    justify-content: center;
    gap: 7rem;
    padding-top: 3rem;
    li {
      list-style: none;
      a {
        text-decoration: none;
        color: #fff;
        font-size: 14px;
      }
      a:hover {
        color: white;
        transition: 0.45s ease-in-out;
        font-size: 16px;
      }
      a::before {
        transition: 0.45s ease-in-out;
      }
    }
  }
}
.sectionTwo {
  display: flex;
  justify-content: space-between;
  padding-top: 5rem;
  .sectionTwoH3 {
    h3 {
      font-size: 14px;
    }
  }
}
.hrefs {
  display: flex;
  gap: 2rem;
  align-items: center;
  padding-bottom: 1rem;
  .instahref {
    background: none;
  }
  a {
    text-decoration: none;
    color: white !important;
  }
  a:hover{
    transition: 0.30s ease-in-out;
    transform: scale(1.1);
  }
}
@media all and (max-width: 500px) {
  .footer {
    width: 100%;
  }
  .se {
    display: flex;
    justify-content: center;  
    flex-direction: column;
  }
  .sectionTwo {
    display: none;
  }
}
