.nn {
  display: flex !important;
  justify-content: space-between !important;
  gap: 12rem;
}
.nav-buttn {
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 1rem;
  background: #873600;
  display: none;
}

.nav-link {
  color: #fff;
  font-size: 14px;
  font-weight: 400;
}

.navbar {
  background: #873600 !important;
  height: 12vh;
}
.navbar-brand {
  width: 8%;
  margin-left: 5rem !important;
  img {
    width: 100%;
    border-radius: 3rem;
  }
}
.navbar-brand:hover {
  transform: scale(1.1);
  transition: 0.45s ease-in-out;
}
.nav-link:hover {
  color: #000;
  transform: scale(1.1);
  transition: 0.45s ease-in-out;
}
.but {
  background: #fff;
  color: #000;
  margin-left: 15rem !important;
  width: 69px;
  font-size: 12px;
}
.but:hover {
  width: 75px;
  font-size: 14px;
  transition: 0.45s ease-in-out;
  background: #000;
  color: #fff;
}
@media all and (max-width: 500px) {
  .nav-buttn {
    display: block;
  }
  .nn {
    gap: 0;
  }
  .nav-link {
    color: #fff;
    display: grid !important;
    grid-template-columns: repeat(2, 1fr);
  }
  .navbar {
    background: #873600;
    height: max-content;
  }
  .navbar-brand {
    width: 15%;
    img {
      width: 90%;
      border-radius: 3rem;
    }
  }
  .nav-link:hover {
    color: #000;
    transform: scale(1.1);
  }
  .but {
    background: #000;
    color: #fff;
    margin: auto;
  }
  .but:hover {
    background: #fff;
    color: #000;
  }
}
@media all and (max-width: 600px) {
  .nav-buttn {
    display: block;
  }
  .nav-link {
    color: #fff;
    display: grid !important;
    grid-template-columns: repeat(2, 1fr);
  }
  .navbar {
    background: #873600;
    height: max-content;
  }
  .ml {
    margin: auto;
  }
  .navbar-brand {
    width: 15%;
    margin-left: 1rem;
    img {
      width: 90%;
      border-radius: 3rem;
    }
  }
  .nav-link:hover {
    color: #000;
    transform: scale(1.1);
  }
  .but {
    background: #000;
    color: #fff;
    margin: auto;
  }
  .but:hover {
    background: #fff;
    color: #000;
    transform: scale(1.1);
  }
}
